<template>
  <div class="sidebar">
    <el-card class="box-card">
      <div slot="header" class="clearfix">
        <span>官方QQ群</span>
      </div>
      <div v-for="o in group" :key="o">
        <span>
          {{'官方群：' + o.number }}
          <a target="_blank" href="//shang.qq.com/wpa/qunwpa?idkey=6b11a33675e2c4e027919039ab554cca96daab9c7451027664cf8fc11d0aeb21"><img border="0" src="//pub.idqqimg.com/wpa/images/group.png"></a>
        </span>
      </div>
      <br/>
    </el-card>
    <br/>
    <el-card class="box-card">
      <div slot="header" class="clearfix">
        <span>站主QQ</span>
      </div>
      <div class="text">
        <div>
          QQ：820155406
        </div>
        <br/>
        <img class="img" src="../../../static/images/qq.jpg"/>
      </div>
    </el-card>
    <br/>
    <el-card class="box-card">
      <div slot="header" class="clearfix">
        <span>求打赏~钱多钱少都挺好</span>
      </div>
      <div class="text">
        <div>
          微信：
        </div>
        <img class="img" src="../../../static/images/wxpay.png"/>
        <div>
          支付宝：
        </div>
        <img class="img" src="../../../static/images/alipay.png"/>
      </div>
    </el-card>
  </div>
</template>

<script>
export default {
  data () {
    return {
      group: [
        {
          'number': '56406336'
        }
      ]
    }
  },
  computed: {
    onRoutes () {
      return this.$route.path.replace('/', '')
    }
  }
}
</script>

<style scoped>
.img {
  padding-left: 20%;
  width: 60%;
}
.text {
  font-size: 16px;
  color: red;
}
</style>



