<template>
  <div>
    <el-container>
      <el-header>
        <v-header></v-header>
      </el-header>
      <el-main>
        <el-row :gutter="24">
          <el-col :span="16" :offset="2">
            <!-- <v-content></v-content> -->
            <router-view></router-view>
          </el-col>
          <el-col :span="4">
            <v-right></v-right>
          </el-col>
        </el-row>
      </el-main>
      <el-footer>
        <v-foot></v-foot>
      </el-footer>
    </el-container>
  </div>
</template>

<script>
import vHeader from './Header.vue'
import vRight from './Right.vue'
import vContent from './Content.vue'
import vFoot from './Foot.vue'
export default {
  data () {
    return {
      screenWidth: document.body.clientWidth
    }
  },
  components: {
    vHeader, vRight, vContent, vFoot
  },
  mounted () {
    const that = this
    window.onresize = () => {
      return (() => {
        window.screenWidth = document.body.clientWidth
        that.screenWidth = window.screenWidth
        console.log(that.screenWidth)
      })()
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.el-header {
  padding-left: 200px;
  background-color: #545c64;
  text-align: center;
  line-height: 60px;
}
.el-main {
  background-color: #E9EEF3;
}
.el-footer {
  background-color: #545c64;
  height: 200px;
  line-height: 10px;
  font-size: 14px;
}
</style>



